import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AccountService } from '@app/_services';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-careerlist',
  templateUrl: './careerlist.component.html',
  styleUrls: ['./careerlist.component.less']
})
export class CareerlistComponent implements OnInit {

  users: any = null;
  name: any;
  value: any;
  selectvalue = "Choose Category";
  pagecount = "Selecte Page per Recode";
  page:any = 1;
  count:any = 0;
  pageSize:any = 2;
  pageSizes:any = [5, 10, 25];
  // pagecount123 ="Selecte....";
  // pagecount :any;
  pageIndex:any =1;
  TagData: any = [];
  TagFilterdata: Array<any> = [];
  TagTempdata: Array<any> = [];
  x=1
  constructor(private accountService: AccountService, private route: Router ) { }
  PageSizeCount(){
    this.pageSize=this.pagecount
  }
  getRequestParams(page, pageSize): any {
    // tslint:disable-next-line:prefer-const
    let params = {};

    if (page) {
      params[`page`] = page;
    }

    if (pageSize) {
      params[`size`] = pageSize;
    }

    return params;
  }
  ngOnInit() {

    this.accountService.getCareerListData().subscribe((TagData) => {
      debugger;
      console.warn(TagData);
      this.TagData = TagData;
      this.TagTempdata = this.TagData;
    })
  }
  deleteCareer(id: any) {
    Swal.fire({
      title: 'Are you sure want to remove?',
      text: 'You will not be able to recover this file!',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, keep it'
    }).then((result) => {
      if (result.value) {
        // this.users.splice(id-1,1);
        this.accountService.DeleteCareer(id).subscribe((result) => {
          // const params = this.getRequestParams(this.page, this.pageSize);
          const params = this.getRequestParams(this.page, this.pageSize);
          this.accountService.BlogTagsgetAll(params)
            .pipe(first())
            .subscribe(
              users => this.users = users,
              count => this.count = count
            );
            this.ngOnInit();
        })
       
        Swal.fire(
          'Deleted!',
          'Your imaginary file has been deleted.',
          'success'
        )
      } else if (result.dismiss === Swal.DismissReason.cancel) {
        Swal.fire(
          'Cancelled',
          'Your imaginary file is safe :)',
          'error'
        )
      }
    })


    // console.warn(item);
    // Swal.fire({
    //   title: 'Are you sure want to Delete?',
    //   text: ' ',
    //   icon: 'warning',
    //   showCancelButton: true,
    //   confirmButtonText: 'Yes, delete it!',
    //   cancelButtonText: 'No, keep it'
    // }).then((result) => {
    //   if (result.value) {
    //     debugger;
    //     this.Tag.deleteTagdata(item).subscribe((data) => {
    //       debugger;
    //       this.ngOnInit();
    //       this.p = this.x
    //     })
    //     Swal.fire(
    //       'Deleted!',
    //       'Your Record has been deleted.',
    //       'success'
    //     )
    //   } else if (result.dismiss === Swal.DismissReason.cancel) {
    //     Swal.fire(
    //       'Cancelled',
    //       ' ',
    //       'error'
    //     )
    //   }
    //   // this.TagData.splice(item - 1, 1);

    // })

  }


  searchValueResto() {
    console.warn(this.selectvalue, this.value);
    if (this.value !== '') {
      this.TagData = this.TagTempdata;
      if (this.selectvalue == 'name') {

        this.TagFilterdata = this.TagData.filter((res: any) => {

          return res.name.toLocaleLowerCase().match(this.value.toLowerCase());
         
        });
        this.p= this.x
      }
      else if (this.selectvalue !== '') {
        this.ngOnInit();
      }
      if (this.TagFilterdata != null) {
        this.TagData = this.TagFilterdata;
      }
    }
    else if (this.value === '') {
      this.ngOnInit();
    }
  }


  //pagination
  p: number = 1;
  // sorting
  reverse: boolean = false;
  key = 'TagId';
  sort(key: string) {
    this.key = key;
    this.reverse = !this.reverse;
  }


}
