import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AccountService, AlertService } from '@app/_services';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-careeradd',
  templateUrl: './careeradd.component.html',
  styleUrls: ['./careeradd.component.less']
})
export class CareeraddComponent implements OnInit {

  form: FormGroup;
  id: string;
  loading = false;
  // submitted = false;
  careerId:string;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private accountService: AccountService,
    private alertService: AlertService
  ) { }
  add = new FormGroup({
    title: new FormControl('',[Validators.required]),
    description: new FormControl('',[Validators.required]),
    designation: new FormControl('',[Validators.required]),
    experience: new FormControl('',[Validators.required]),
    location: new FormControl('',[Validators.required]),
    education: new FormControl('',[Validators.required]),
    sortOrder: new FormControl('',[Validators.required]),
    isActive: new FormControl(''),
  })
  ngOnInit() {
    this.accountService.careerById(this.route.snapshot.params.id).subscribe((result: any) => {
      console.warn(result);
      debugger
      this.add = new FormGroup({
        title: new FormControl(result['title']),
        description: new FormControl(result['description']),
        designation: new FormControl(result['designation']),
        experience: new FormControl(result['experience']),
        location: new FormControl(result['location']),
        education: new FormControl(result['education']),
        sortOrder: new FormControl(result['sortOrder']),
        isActive: new FormControl(result['isActive']),
        careerId: new FormControl(result['careerId']),
      })
    })


  }

  // convenience getter for easy access to form fields
  // get f() { return this.add.controls; }

  onSubmit() {
    // debugger;
    // this.submitted = true;

    // // reset alerts on submit
    // this.alertService.clear();

    // // stop here if form is invalid
    // if (this.add.invalid) {
    //   return;
    // }
    if (this.careerId="") {
      console.warn(this.add.value)
      debugger;
      this.loading = true;
      this.accountService.AddCareerData(this.add.value)
        .pipe(first())
        .subscribe({
          next: () => {
            this.alertService.success('BlogTags added successfully', { keepAfterRouteChange: true });
            this.router.navigate(['/admin/career'], { relativeTo: this.route });
          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });
     
    }
    else{
      this.loading = true;
      this.accountService.UpdateCareerData(this.id, this.add.value)
      .pipe(first())
      .subscribe({
        next: () => {
          debugger
          this.alertService.success('Update successful', { keepAfterRouteChange: true });
          this.router.navigate(['/admin/career'], { relativeTo: this.route });
        },
        error: error => {
          this.alertService.error(error);
          this.loading = false;
        }
      });
    }
    Swal.fire(
      'Done!',
      'Your record has been successfully added',
      'success'
    )



  }
}
