import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CareeraddComponent } from './careeradd/careeradd.component';
import { CareerlistComponent } from './careerlist/careerlist.component';

const routes: Routes = [
  {
    path: '', 
    children: [
        { path: '', component: CareerlistComponent  },
        { path: 'add', component: CareeraddComponent },
        { path: 'add/:id', component: CareeraddComponent }

    ]
}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CareerRoutingModule { }
